angular.module('ppApp').controller('costChartCtrl', costChartCtrl);

costChartCtrl.$inject = ['patent', 'ca','$scope', '$timeout', '$state', 'organiseTextService', '$location', 'currentTransactionsService', '$anchorScroll']

function costChartCtrl(patent, ca, $scope, $timeout, $state, organiseTextService, $location, currentTransactionsService, $anchorScroll) {

    var vm = this;

    vm.patent = patent;
    vm.barData = barData;
    vm.barOptions = {
        chart: {
            type: 'multiBarHorizontalChart',
            barColor: ["#e30613", "#f9b233", "#53ab58"],
            tooltip: {
              hideDelay: 0
            },                 
            showControls: false,
            showValues: true,
            showLegend: false,
            stacked: true,
            duration: 500,
            margin: {
                left: 70,
                right: 10
            },
            x: function(d){
                return d[0];
            },
            y: function(d){
                return d[1];
            },
            multibar: {
                groupSpacing: 0.4
            },
            xAxis: {
                showMaxMin: false,
                axisLabelDistance: 0,
                staggerLabels: false,
                rotateLabels: 0,
                rotateYLabel: true,
                axisLabel: null,
                height: 60,
                ticks: null,
                width: 2000,
                margin: {
                    top: 0,
                    right: 0,
                    bottom: 0,
                    left: 0
                },
                duration: 250,
                orient: 'left',
                tickValues: null,
                tickSubdivide: 0,
                tickSize: 6,
                tickPadding: 5,
                domain: [
                    0,
                    1
                ],
                range: [
                    0,
                    1
                ],             
                tickFormat: function (d, i) {
                    return d3.time.format('%x')(new Date(d));
                }
            },
            yAxis: {
                showMaxMin: true,
                axisLabelDistance: 20,
                staggerLabels: false,
                rotateLabels: 0,
                rotateYLabel: true,
                height: 60,
                ticks: null,
                width: 75,
                margin: {
                    top: 0,
                    right: 0,
                    bottom: 0,
                    left: 0
                },
                duration: 250,
                orient: 'bottom',
                tickValues: null,
                tickSubdivide: 0,
                tickSize: 6,
                tickPadding: 3,
                domain: [
                    0,
                    1
                ],
                range: [
                    0,
                    1
                ],                 
                tickFormat: function(d){
                    return '$ ' + d3.format('.02f')(d);
                }
            },
            callback: function(d, e) {
                d3.selectAll('.nvd3 .nv-bar rect').attr("rx", 20);
            }
        }
    }

    function barData() {

        var barChartArr = [], label = [], data = [];
        for(var property in ca) {
            if(ca.hasOwnProperty(property)) {
                var dayData = ca[property];
                if((property.includes('StartDate')) && (!property.includes ('UI'))) {
                    label.push(dayData);
                }
                if(property.includes('Cost')) {
                    data.push(dayData);
                }
            }
        }

        for (var i = 0; label.length && i < data.length; i++) {
            barChartArr[i] = [label[i], data[i]]; //pairs the items from two arrays into a single array in the new array
        }

        barChartArr.reverse();

        return [
            {
                'key': 'Cost',
                'values': barChartArr
            }
        ]

    }

}