angular.module('ppApp').factory('costAnalysisService', costAnalysisService);

costAnalysisService.$inject = ['$http', '$q'];

function costAnalysisService($http, $q) {

    var factory = {
        fetchEuroPctCa:  fetchEuroPctCa,
        fetchRenewalCa: fetchRenewalCa,
        fetchCa: fetchCa
    }

    function fetchCa(patent) {
        
        var array = [];
        var deferred = $q.defer();
        patent.portfolioUI.serviceList.forEach(function(data){
            if(data.serviceType == 'Form1200') {
                array.push($http.get(ppdomain+'rest-form1200-cost-analysis/'+patent.id))

            }
            if(data.serviceType == 'Renewal') {
                array.push($http.get(ppdomain+'rest-cost-analysis/'+patent.id))
            }
        })

        return $q.all(array).then(function(data){
            return data;
        })

        // return 
    }

    function fetchEuroPctCa(patent) {

        if(patent.portfolioUI.serviceList[0].serviceType = 'Form1200') {
            var deferred = $q.defer();
            $http.get(ppdomain+'rest-form1200-cost-analysis/'+patent.id)
                .then(
                function (response) {
                    deferred.resolve(response.data);
                },
                function(errResponse){
                    deferred.reject(errResponse);
                }
            );

            return deferred.promise;

        }


    };

    function fetchRenewalCa(patent) {
        if(patent.portfolioUI.serviceList[0].serviceType = 'Renewal') { 
            var deferred = $q.defer();
            $http.get(ppdomain+'rest-cost-analysis/'+patent.id)
                .then(
                function (response) {
                    deferred.resolve(response.data);
                },
                function(errResponse){
                    deferred.reject(errResponse);
                }
            );

            return deferred.promise;

        }

    };    

    return factory;

}